
body {

    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    font-family: 'archivo';
}

.MainDiv {
    width: 90vw;
    height: 100vh;
    border-style: none;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    background-color: transparent;
}

.FlexDiv {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

.QuestDiv {
    width: 50vw;
    height: 10vh;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
    margin-bottom: 100px;
    color: #282cb9;
}

.InputText {
    width: 250px;
    height: 32px;
    border-style: none;
    border-bottom: solid gray;
    margin-top: 24px;
    font-size: 24px;
}

.InputText:active {
    border-left: solid gray;
}

.SubmitBtn {
    border-style: none;
    width: 250px;
    height: 32px;
    color: black;
    background-color: lightblue;
    margin: 10px;
    font-size: 15px;
}

.SubmitBtn:hover {
    transform: scale(1.1);
    cursor: pointer;
    background-color: rgb(53, 184, 228);
}

.WrapForm {
    width: 50vw;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    background-color: rgba(255, 255, 255, 0.95);
    padding: 32px;
    border-radius: 5px;
}

@media (max-width: 800px) {
    .QuestDiv {
        width: 90vw;
    }
    .FlexDiv {
        flex-direction: column;
    }
    .WrapForm {
        width: 70vw;
    }
}